@import "../../utils/commons.scss";

.container {
  border-radius: 150px;
  width: 150%;
  padding: 30px;
  color: var(--clr-text-dark);
  align-items: center;
  .textContainer {
    @extend .flexCol;
    width: 40%;
    margin: 0px 7%;
    ul {
      li {
        font-size: 1.5rem;
        margin: 10px;
      }
    }
  }
  h4 {
    color: var(--clr-heading-dark);
    font-weight: 900;
    font-size: 2.5rem;
  }
  .imageContainer {
    width: 200px;
    svg {
      width: 100%;
      object-fit: cover;
    }
  }
  @media screen and (max-width: 800px) {
    .textContainer {
      margin: 0 3%;
    }
  }
  @media screen and (max-width: 700px) {
    .textContainer {
      margin-right: 0%;
    }
  }
  @media screen and (max-width: 620px) {
    width: 100%;
    border-radius: 20px !important;
    flex-direction: column !important;
    margin: 5% !important;
    padding: 0px 10px;
    .textContainer {
      width: 100%;
      padding: 0px 20px;
      padding-bottom: 10px;
      margin: 0;
    }
  }
  @media screen and (max-width: 600px) {
    margin: 5% 0% !important  ;
  }
}
